import React from 'react';

const Context = React.createContext('Binary');

export class ConvertStore extends React.Component {

  state = { converter: 'Binary'};

  onConvertChange = (converter) => {
    this.setState({converter});
  }

  render() {
    return(
      <Context.Provider value={{...this.state, onConvertChange: this.onConvertChange }}>
        {this.props.children}
      </Context.Provider>
    );
  }
}

export default Context;

