import React from "react";
import Button from "./Button";
import TextArrea from "./TextArrea";
import Header from "./Header";

const conversionConfig = {
  decToHex: {
    placeholder: "Enter decimal number here",
    function: "decToHex"
  },
  hexToDec: {
    placeholder: "Enter hexadecimal number here",
    function: "hexToDec"
  }
};
class Calculator extends React.Component {
  state = {
    value: "",
    convertedValue: "",
    conversionType: "decToHex"
  };

  inputReceiver = event => {
    const input = event.target.value;
    this.setState({ value: input });
  };
  decToHex = () => {
    let val = this.state.value;
    if (val.length !== 0) {
      val = parseInt(val);
      let hex = "";
      let newVal = val;
      let remainder;

      while (newVal !== 0) {
        remainder = newVal % 16;
        newVal = Math.floor((newVal /= 16));
        //console.log(remainder);
        switch (remainder) {
          case 10:
            remainder = "A";
            break;
          case 11:
            remainder = "B";
            break;
          case 12:
            remainder = "C";
            break;
          case 13:
            remainder = "D";
            break;
          case 14:
            remainder = "E";
            break;
          case 15:
            remainder = "F";
            break;
          default:
            break;
        }
        hex = remainder + hex;
      }

      //textArea.innerHTML = '';
      this.setState({ convertedValue: hex });
    }
    this.setState({ value: "" });

    console.log("CONVERTING DECIMAL TO BINARY!");
  };

  hexToDec = () => {
    let textArea = document.getElementById("textarr");
    let val = this.state.value;
    val += "";
    let temp;
    let newVal = 0;
    let length = val.length;
    if (val.length !== 0) {
      for (let i = 0; i < length; i++) {
        temp = (val.charAt(i));
        switch (temp) {
          case 'A':
              temp = 10;
            break;
          case 'B':
              temp = 11;
            break;
          case 'C':
              temp = 12;
            break;
          case 'D':
              temp = 13;
            break;
          case 'E':
              temp = 14;
            break;
          case 'F':
              temp = 15;
            break;
          default:
            temp = parseInt(temp);
            break;
        }

        //console.log(temp);
        newVal += temp * Math.pow(16, length - 1 - i);
      }
      //textArea.innerHTML = '';
      textArea.value = "";
      this.setState({ convertedValue: newVal });
      //console.log(newVal);
    }

    this.setState({ value: "" });
  };
  // binToDec = () => {
  //   console.log('CONVERTING BINARY TO DECIMAl!');

  // }

  swap = () => {
    if (this.state.conversionType === "decToHex") {
      this.setState({ conversionType: "hexToDec" });
    } else {
      this.setState({ conversionType: "decToHex" });
    }

    this.setState({ value: "", convertedValue: "" });

    //console.log(swapBtn.onclick);
  };

  render() {
    return (
      <div>
        <Header converter="hex" />
        <div className="calc">
          <TextArrea
            value={this.state.value}
            onChange={this.inputReceiver}
            placeholder={
              conversionConfig[this.state.conversionType].placeholder
            }
            readonly={false}
          />
          <div className="buts">
            <Button
              id="sub"
              butType="Submit"
              onClick={eval(
                "this." + conversionConfig[this.state.conversionType].function
              )}
            />
            <Button id="swap" butType="Swap" onClick={this.swap} />
          </div>
          <TextArrea
            value={this.state.convertedValue}
            placeholder=""
            readonly={true}
          />
        </div>
      </div>
    );
  }
}

export default Calculator;
