import React from 'react';
class Button extends React.Component {


  call = (e) => {
    e.target.blur();
    this.props.onClick();
  }

  onTouchStartHandler = (e) => {
    e.target.style.backgroundColor = '#5a6268';
    e.target.style.borderColor = '#545b62';
  }
  onTouchEndHandler = (e) => {
    e.target.style.backgroundColor = '#6c757d';
    e.target.style.borderColor = '#6c757d';
  }


  render(){
    return(
      <button id={this.props.id} className="btn btn-secondary" onTouchStart={(e) => {this.onTouchStartHandler(e)}} onTouchEnd={(e) => {this.onTouchEndHandler(e)}} onClick={(e) => this.call(e)} >{this.props.butType}</button>
    );
  }
}

export default Button;

